var recipes={'meta':{'table':'recipes','type':'collection','total':6,'total_entries':6},'data':[{'id':1,'name':'Crepes','description':"Cheap, easy, but most importantly, gourmet without the hassle. Wow your friends with this classic upscale pancake and make them think you're a chef. This recipes will serve as the base to any sweet breakfast or savory dinner.",'ingredients':'<p>Batter:</p>\n<ul>\n<li>1 cup all-purpose flour</li>\n<li>2 eggs</li>\n<li>1/2 cup of milk</li>\n<li>1/2 cup of water</li>\n<li>1/4 teaspoon of salt</li>\n<li>2 tablespoons melted butter</li>\n<li>2 tablespoons of sugar (optional)</li>\n</ul>\n<p>For breakfast crepes, fill with fresh fruit, chocolate chips, powdered sugar, brown sugar, whipped cream, nuetella, cream-cheese frosting, syrup, etc.</p>','directions':'<p>Mix flour, salt and sugar in large bowl. Add eggs, milk, water, and melted butter, whisking thoroughly. Pour into a spray-greased pan on medium heat (5-6) using most of a 1/2 cup measuring instrument. Swirl around until a thin layer coats the pan. Allow cook for a couple minutes. Do NOT flip over. Fill with toppings and slowly roll. Plate with care!</p>','image':{'meta':{'table':'directus_files','type':'item'},'data':{'id':5,'status':1,'name':'00000000005.jpg','title':'photo 2018 06 18 00 04 55','location':'','caption':'','type':'image/jpeg','charset':'binary','tags':'','width':930,'height':759,'size':95155,'embed_id':null,'user':1,'date_uploaded':'2018-06-18T00:05:25-04:00','storage_adapter':'local','url':'/storage/uploads/00000000005.jpg','thumbnail_url':'/storage/uploads/thumbs/5.jpg','old_thumbnail_url':'/storage/uploads/thumbs/00000000005-jpg-160-160-true.jpg','html':null}}},{'id':2,'name':'Chicken Siciliano','description':'Flavor overload. A take on thick, chunky and spicy southern Italian marinara sauce. Amazing flavor that pairs well with chicken or calamari. A great way to flavor jar pasta sauce from the store without crushing tomatoes yourself for hours.','ingredients':"<p>Sauce:</p>\n<ul>\n<li>1/2 green bell pepper, finely diced</li>\n<li>1 white onion, chopped</li>\n<li>4/5 jar hot cherry peppers</li>\n<li>1 can diced tomatoes with green chilis</li>\n<li>1 handful of yellow grape tomatoes, quartered</li>\n<li>1 tablespoon olive oil</li>\n<li>2 tablespoons butter</li>\n<li>1 tablespoon salt</li>\n<li>2 tablespoons red pepper flakes</li>\n<li>1 tablespoon Italian seasoning</li>\n<li>1 jar Trader Joe's plain marinara</li>\n</ul>\n<p>Chicken:</p>\n<ul>\n<li>3 chicken breasts</li>\n<li>2 eggs</li>\n<li>3 cups all-purpose flour</li>\n<li>4 cups breadcrumbs</li>\n<li>1 tablespoon parsley</li>\n<li>2 tablespoons salt</li>\n<li>1/2 cup vegetable oil</li>\n<li>2 tablespoons butter</li>\n<li>6 slices of mozzarella&nbsp;</li>\n</ul>",'directions':'<p>Sautee white onion and green bell pepper in oil and butter until pepper is soft. Add grape tomatoes, hot cherry peppers. Continue to sautee until vegetables congeal into sauce. Add to large pot, add marinara sauce, let simmer.</p>\n<p>Meanwhile, put flour, eggs, breadcrumbs in separate containers, season breadcrumbs with parsley. Halve chicken breasts. Dredge in flour, egg, and coat in breadcrumbs.</p>\n<p>Add vegetable oil to pan, allow time to heat. Fry chicken in pan, adding butter as desired. Move chicken to glass tray (oven-safe). Salt chicken immediately once out of oil. Preheat oven to 425 degrees about halfway through frying. Layer chicken in the tray, pour sauce over top and coat with mozzarella. Bake for 5-10 minutes.</p>','image':{'meta':{'table':'directus_files','type':'item'},'data':{'id':6,'status':1,'name':'00000000006.jpg','title':'photo 2018 06 18 01 26 45','location':'','caption':'','type':'image/jpeg','charset':'binary','tags':'','width':1280,'height':1023,'size':234576,'embed_id':null,'user':1,'date_uploaded':'2018-06-18T01:28:13-04:00','storage_adapter':'local','url':'/storage/uploads/00000000006.jpg','thumbnail_url':'/storage/uploads/thumbs/6.jpg','old_thumbnail_url':'/storage/uploads/thumbs/00000000006-jpg-160-160-true.jpg','html':null}}},{'id':3,'name':'Nutella Butter and Banana Cream Cheese Stuffed French Toast','description':"Flavor overload. The creaminess of the banana cream cheese with the sweetness from the Nutella butter will render you in a food coma for the rest of the day. Don't consume if you want to be productive.",'ingredients':'<ul>\n<li>4 pieces of thickly sliced bread</li>\n<li>1 cup milk</li>\n<li>2 eggs</li>\n<li>1 tablespoon cinnamon</li>\n<li>2 teaspoons of vanilla extract</li>\n<li>1 cup Nutella</li>\n<li>8oz whipped cream cheese</li>\n<li>1 banana</li>\n<li>1 teapsoon confectioner sugar</li>\n<li>4 tablespoons butter</li>\n<li>Cooking spray</li>\n</ul>','directions':'<p>Mash the banana and mix with the cream cheese and 1 teapsoon of vanilla extract thoroughly. Cut the pieces of bread open and stuff with the cream cheese mixture. Dip in mixture of egg, milk, vanilla extract and cinnamon. Fry and top with warm Nutella and butter mixture.</p>','image':{'meta':{'table':'directus_files','type':'item'},'data':{'id':7,'status':1,'name':'00000000007.jpg','title':'french toast','location':'','caption':'','type':'image/jpeg','charset':'binary','tags':'','width':1280,'height':1224,'size':155313,'embed_id':null,'user':1,'date_uploaded':'2018-06-23T16:16:43-04:00','storage_adapter':'local','url':'/storage/uploads/00000000007.jpg','thumbnail_url':'/storage/uploads/thumbs/7.jpg','old_thumbnail_url':'/storage/uploads/thumbs/00000000007-jpg-160-160-true.jpg','html':null}}},{'id':4,'name':'Pancakes','description':"Light, fluffy crowd pleasers. Even our lactose intolerant friends eat them. It's worth it.",'ingredients':'<ul>\n<li>1 1/2 cups all-purpose flour</li>\n<li>3 1/2 teaspoons baking powder</li>\n<li>1 teaspoon salt</li>\n<li>1 tablespoon white sugar</li>\n<li>1 1/4 cup milk</li>\n<li>1 egg</li>\n<li>3 tablespoons butter, melted</li>\n</ul>','directions':'<p>In a large bowl, sift together the flour, baking powder, salt and sugar. Make a well in the center and pour in the milk, egg and melted butter; mix until smooth.</p>\n<p>Heat a lightly oiled griddle or frying pan over medium high heat. Pour or scoop the batter onto the griddle, using approximately 1/4 cup for each pancake. Brown on both sides and serve hot.</p>\n<p>Makes ~10 pancakes.</p>','image':{'meta':{'table':'directus_files','type':'item'},'data':{'id':8,'status':1,'name':'00000000008.jpg','title':'photo 2018 09 11 19 26 58','location':'','caption':'','type':'image/jpeg','charset':'binary','tags':'','width':744,'height':596,'size':71360,'embed_id':null,'user':1,'date_uploaded':'2018-09-11T19:28:37-04:00','storage_adapter':'local','url':'/storage/uploads/00000000008.jpg','thumbnail_url':'/storage/uploads/thumbs/8.jpg','old_thumbnail_url':'/storage/uploads/thumbs/00000000008-jpg-160-160-true.jpg','html':null}}},{'id':5,'name':'Chocolate Covered Strawberries','description':'Looking to be romantic without breaking the bank? Just hungry? Trying to be at least somewhat healthy? Chocolate. Covered. Strawberries. Need I say more?','ingredients':'<ul>\n<li>1 carton of strawberries (1 pound)</li>\n<li>4 ounces of dark chocolate chips</li>\n<li>4 ounces of semisweet chocolate chips</li>\n<li>3 ounces of white chocolate chips</li>\n</ul>','directions':'<p>Melt the chocolate in individual bowls on half power in the microwave until the chocolate is smooth. Line a baking sheet with wax paper. Hold each strawberry by the stem, dip and twist slightly as you lift out of the chocolate, placing on the baking sheet. Alternate dipping between dark and semisweet chocolate. Drizzle with white chocolate using a utensil. Stick in freezer for several minutes and then in refridgerator for thirty minutes.</p>','image':{'meta':{'table':'directus_files','type':'item'},'data':{'id':9,'status':1,'name':'00000000009.jpg','title':'photo 2018 09 11 19 26 53','location':'','caption':'','type':'image/jpeg','charset':'binary','tags':'','width':1080,'height':1064,'size':243548,'embed_id':null,'user':1,'date_uploaded':'2018-09-11T23:33:55-04:00','storage_adapter':'local','url':'/storage/uploads/00000000009.jpg','thumbnail_url':'/storage/uploads/thumbs/9.jpg','old_thumbnail_url':'/storage/uploads/thumbs/00000000009-jpg-160-160-true.jpg','html':null}}},{'id':6,'name':'Chilli Beef Lettuce Wraps','description':'The heat from the chiles, saltiness of the soy sauce and sweetness from the brown sugar will surely please any palate. The texture on this dish is not forgotten; the lettuce is crips and the spring onions provide a refreshing start to each bite. Better yet, this is a one pan recipe.','ingredients':'<p>Beef:</p>\n<ul>\n<li>Olive oil, for frying</li>\n<li>1 lb 80/20 ground beef</li>\n<li>Toasted sesame oil, for frying</li>\n<li>2 garlic cloves, peeled and finely chopped</li>\n<li>2 inches piece of fresh root ginger, peeled and finely chopped</li>\n<li>3 red chillies, deseeded and chopped</li>\n<li>2 tbsp light brown sugar</li>\n<li>1 tbsp fish sauce</li>\n<li>Zest of 1 lime, juice of 1/3rd</li>\n<li>3 spring onions, trimmed and chopped</li>\n<li>Sea salt and freshly ground black pepper</li>\n<li>2 little gem lettuces, separated into leaves, to serve</li>\n</ul>\n<p>Dressing:</p>\n<ul class="recipe-division">\n<li>1 tbsp soy sauce&nbsp;</li>\n<li>Juice of 1/2 lime</li>\n<li>1 tsp sesame oil&nbsp;</li>\n<li>1/2 red chilli, thinly sliced</li>\n<li>Small bunch of cilantro leaves, chopped</li>\n<li>1 tsp fish sauce, to taste</li>\n<li>1 tsp light brown sugar</li>\n<li>1 tbsp olive oil</li>\n</ul>','directions':'<p>Heat a large frying pan and add a little oil. Season beef with salt and pepper. Fry the mince in the hot pan for 5&ndash;7 minutes until crisp and brown and broken down to a fine consistency. Drain the crisped mince in a sieve &ndash; this will help it stay crispy. Set aside.</p>\n<p>Wipe out the pan and add a tablespoon of toasted sesame oil. Add the garlic, ginger and chilli. Fry with a pinch of salt and the sugar for 2 minutes. Add the drained mince and stir to mix.</p>\n<p>Add the fish sauce and heat through. Stir in the lime zest and juice, then add the spring onions, stirring for 30 seconds. Turn off the heat.</p>\n<p>Mix all the dressing ingredients together and adjust to taste.</p>\n<p>To serve, spoon some of the mince mixture into the lettuce leaves, drizzle with a little dressing and serve.</p>','image':{'meta':{'table':'directus_files','type':'item'},'data':{'id':10,'status':1,'name':'00000000010.jpg','title':'photo 2018 09 11 19 27 11','location':'','caption':'','type':'image/jpeg','charset':'binary','tags':'','width':960,'height':1280,'size':190867,'embed_id':null,'user':1,'date_uploaded':'2018-09-11T23:48:04-04:00','storage_adapter':'local','url':'/storage/uploads/00000000010.jpg','thumbnail_url':'/storage/uploads/thumbs/10.jpg','old_thumbnail_url':'/storage/uploads/thumbs/00000000010-jpg-160-160-true.jpg','html':null}}}],'public':true};

